// Main Stylesheet
@import
  "_font-face";


// Colors
$primary-color          : #F2F2F2;
$secondary-color        : #000;
$accent-color           : #132410;
$shade-color            : #354232;
$complementary-color    : 2f2f2f;

// Fonts
$body-font      : DINPro, sans-serif;
$heading-font   : DINPro, sans-serif;
$body-font-size : 1.1rem;
$base-line-height: 1.8;

body {
  color: $primary-color;
}

@import
  "_resets",
  "_utility",
  "_universal",
  "_mixins",
  "_buttons",
  "_nav",
  "_footer",
  "_header",
  "_about",
  "_portfolios",
  "_portfolio-showcase",
  "_media-queries";